import React from "react";
import ProfileContact from "./ProfileContact";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Button from "muicss/lib/react/button";
import Expire from "../common/Expire";
import AddContactModal from "./AddContactModal";
import HideProfileConfirmModal from "./HideProfileConfirmModal";

class ProfilePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      phone_contact_info: [],
      email_contact_info: [],
      fax_contact_info: [],
      addContactHidden: true,
      modalOpen: false,
      isFaxModal: false,
      isEmailModal: false,
      isPhoneModal: false,
      modalType: null,
      confirmHideProfileModalOpen: false,
    };

    this.handleToggle = this.handleToggle.bind(this);
    this.openModal = this.openModal.bind(this);
    this.openFaxModal = this.openFaxModal.bind(this);
    this.openEmailModal = this.openEmailModal.bind(this);
    this.openPhoneModal = this.openPhoneModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.addContactClick = this.addContactClick.bind(this);
    this.confirmHideProfile = this.confirmHideProfile.bind(this);
  }

  UNSAFE_componentWillMount() {
    this.setState({
      phone_contact_info:
        this.props.profile.phoneInfo.map((i) => ({
          cp_id: i.cpId,
          contact_type: parseInt(i.contactType, 10),
          description: i.value,
          hide_from_directory: i.directoryHide ? 1 : 0,
          is_primary: i.isPrimary,
        })) || [],
      email_contact_info:
        this.props.profile.emailInfo.map((i) => ({
          cp_id: i.cpId,
          contact_type: parseInt(i.contactType, 10),
          description: i.value,
          hide_from_directory: i.directoryHide ? 1 : 0,
          is_primary: i.isPrimary,
        })) || [],
      fax_contact_info:
        this.props.profile.faxInfo.map((i) => ({
          cp_id: i.cpId,
          contact_type: parseInt(i.contactType, 10),
          description: i.value,
          hide_from_directory: i.directoryHide ? 1 : 0,
          is_primary: i.isPrimary,
        })) || [],
    });
  }

  openModal() {
    this.setState({
      modalOpen: true,
    });
  }

  openFaxModal() {
    this.setState({
      modalOpen: true,
      modalType: "fax",
    });
  }

  openEmailModal() {
    this.setState({
      modalOpen: true,
      modalType: "email",
    });
  }

  openPhoneModal() {
    this.setState({
      modalOpen: true,
      modalType: "phone",
    });
  }

  closeModal() {
    this.setState({
      addContactHidden: true,
      modalOpen: false,
      isFaxModal: false,
      isEmailModal: false,
      isPhoneModal: false,
      modalType: "",
    });
  }

  handleToggle() {
    if (this.props.directoryStatus === 0) {
      this.setState({ confirmHideProfileModalOpen: true });
    } else {
      this.props.toggleDirectoryHide(0);
    }
  }

  confirmHideProfile(confirm) {
    if (confirm) {
      this.props.toggleDirectoryHide(1);
    }
    this.setState({ confirmHideProfileModalOpen: false });
  }

  setAsMain(cp_id, category) {
    switch (category) {
      case "phone":
        let newPhoneInfo = this.state.phone_contact_info;
        newPhoneInfo.forEach((c) => (c.is_primary = false));
        newPhoneInfo.find((c) => c.cp_id === cp_id).is_primary = 1;
        this.setState({ phone_contact_info: newPhoneInfo });
        break;
      case "email":
        let newEmailInfo = this.state.email_contact_info;
        newEmailInfo.forEach((c) => (c.is_primary = false));
        newEmailInfo.find((c) => c.cp_id === cp_id).is_primary = 1;
        this.setState({ email_contact_info: newEmailInfo });
        break;
      case "fax":
        let newFaxInfo = this.state.fax_contact_info;
        newFaxInfo.forEach((c) => (c.is_primary = false));
        newFaxInfo.find((c) => c.cp_id === cp_id).is_primary = 1;
        this.setState({ fax_contact_info: newFaxInfo });
        break;
      default:
        break;
    }
    this.props.updateContact(
      this.props.userData.contact_id,
      this.state.phone_contact_info,
      this.state.email_contact_info,
      this.state.fax_contact_info
    );
  }

  showHideContact(cp_id, category, hide) {
    switch (category) {
      case "phone":
        let newPhoneInfo = this.state.phone_contact_info;
        newPhoneInfo.find((c) => c.cp_id === cp_id).hide_from_directory = hide;
        this.setState({ phone_contact_info: newPhoneInfo });
        break;
      case "email":
        let newEmailInfo = this.state.email_contact_info;
        newEmailInfo.find((c) => c.cp_id === cp_id).hide_from_directory = hide;
        this.setState({ email_contact_info: newEmailInfo });
        break;
      case "fax":
        let newFaxInfo = this.state.fax_contact_info;
        newFaxInfo.find((c) => c.cp_id === cp_id).hide_from_directory = hide;
        this.setState({ fax_contact_info: newFaxInfo });
        break;
      default:
        break;
    }
    this.props.updateContact(
      this.props.userData.contact_id,
      this.state.phone_contact_info,
      this.state.email_contact_info,
      this.state.fax_contact_info
    );
  }

  addContactClick() {
    this.setState({ addContactHidden: !this.state.addContactHidden });
  }

  render() {
    let toast = null;
    if (this.props.toast && this.props.toast.show) {
      toast = (
        <Expire closeToast={this.props.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.toast.message}</p>
            <Button variant="flat" onClick={this.props.closeToast}>
              {this.props.toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }
    const types = {
      1: "Office",
      4: "Office",
      7: "Company",
      8: "Home",
      9: "Home",
      10: "Cell",
      13: "Work",
      15: "Personal",
    };

    var str = this.props.profile.fullName;
    var matches = str.match(/\b(\w)/g);
    var acronym = "";
    acronym = matches ? matches.join("") : "";

    return (
      <Row>
        <Col md="12">
          <div className="profile-page">
            {toast}
            <div className="panel mui-panel profile-header-card">
              <div className="profile-initials">
                <p>{acronym}</p>
              </div>
              <div>
                <p className="page-header">{this.props.profile.fullName}</p>
                <p className="small-title">
                  {this.props.profile.corporateTitle}
                  {this.props.profile.corporateTitle &&
                  this.props.profile.companyName ? (
                    <span> &bull; </span>
                  ) : (
                    ""
                  )}
                  {this.props.profile.companyName}
                </p>
              </div>
            </div>
            <Row className="profile-page-columns">
              <Col lg="7" md="12">
                <p className="profile-section-title">Personal Information</p>
                <div className="panel mui-panel personal-info-panel">
                  {this.state.phone_contact_info.map((contact) => (
                    <ProfileContact
                      category="phone"
                      key={contact.cp_id}
                      typeCode={contact.contact_type}
                      type={types[contact.contact_type]}
                      contact={contact.description}
                      isPrimary={contact.is_primary}
                      hide={contact.hide_from_directory}
                      cpId={contact.cp_id}
                      setAsMain={this.setAsMain.bind(this)}
                      showHideContact={this.showHideContact.bind(this)}
                      deleteContact={this.props.deleteContact}
                    />
                  ))}

                  {this.state.email_contact_info.map((contact) => (
                    <ProfileContact
                      category="email"
                      key={contact.cp_id}
                      typeCode={contact.contact_type}
                      type={types[contact.contact_type]}
                      contact={contact.description}
                      isPrimary={contact.is_primary}
                      hide={contact.hide_from_directory}
                      cpId={contact.cp_id}
                      setAsMain={this.setAsMain.bind(this)}
                      showHideContact={this.showHideContact.bind(this)}
                      deleteContact={this.props.deleteContact}
                    />
                  ))}

                  {this.state.fax_contact_info.map((contact) => (
                    <ProfileContact
                      category="fax"
                      key={contact.cp_id}
                      typeCode={contact.contact_type}
                      type={types[contact.contact_type]}
                      contact={contact.description}
                      isPrimary={contact.is_primary}
                      hide={contact.hide_from_directory}
                      cpId={contact.cp_id}
                      setAsMain={this.setAsMain.bind(this)}
                      showHideContact={this.showHideContact.bind(this)}
                      deleteContact={this.props.deleteContact}
                    />
                  ))}

                  <Row>
                    <Col lg="3" md="3" sm="4" xs="4">
                      <p className="profile-info-header">
                        <i className="material-icons">location_on</i>Address
                      </p>
                    </Col>
                    <Col lg="8" md="8" sm="7" xs="7">
                      <div>{this.props.profile.address}</div>
                    </Col>
                  </Row>
                </div>
              </Col>

              <Col lg="5" md="12">
                <p className="profile-section-title">Settings</p>
                <div className="panel mui-panel profile-settings-panel">
                  <Row>
                    <Col lg="12">
                      <p className="profile-info-header no-padding-bottom">
                        Hide full profile from directory
                      </p>
                      <div className="row press">
                        <input
                          type="checkbox"
                          id="unchecked"
                          className="cbx hidden"
                          onChange={this.handleToggle}
                          checked={Number(this.props.directoryStatus)}
                        />
                        <label htmlFor="unchecked" className="lbl" />
                      </div>
                    </Col>
                  </Row>
                  <p className="settings-footer">
                    To ensure you are properly marketed and maintain maximum
                    exposure when searched, your information should be updated
                    and visible.
                  </p>
                </div>
              </Col>
            </Row>

            <div className="add-info-dropdown">
              <img
                src="/images/icons/add-circle.png"
                className={
                  this.state.addContactHidden ? "add-circle-icon" : "hidden"
                }
                onClick={this.addContactClick}
                alt="add-circle"
              />
              <img
                src="/images/icons/close-circle.png"
                className={
                  this.state.addContactHidden ? "hidden" : "close-circle-icon"
                }
                onClick={this.addContactClick}
                alt="close-circle"
              />
              <div
                className={
                  this.state.addContactHidden
                    ? "hidden"
                    : "add-info-dropdown-content"
                }
              >
                <div onClick={this.openFaxModal}>
                  <img
                    src="/images/icons/fax5-small.png"
                    className="fax-icon"
                    alt="fax"
                  />
                </div>
                <div onClick={this.openEmailModal}>
                  <i className="material-icons email-icon">mail_outline</i>
                </div>
                <div onClick={this.openPhoneModal}>
                  <i className="material-icons phone-icon">phone</i>
                </div>
              </div>
            </div>
            <AddContactModal
              isOpen={this.state.modalOpen}
              onRequestClose={this.closeModal}
              onAfterOpen={() => {}}
              isFax={this.state.isFaxModal}
              isEmail={this.state.isEmailModal}
              isPhone={this.state.isFaxModal}
              type={this.state.modalType}
              addContact={this.props.addContact}
            />
            <HideProfileConfirmModal
              userData={this.props.userData}
              isOpen={this.state.confirmHideProfileModalOpen}
              onRequestClose={this.confirmHideProfile}
              onAfterOpen={() => {}}
            />
          </div>
        </Col>
      </Row>
    );
  }
}

export default ProfilePage;
