import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as profileActions from "../../actions/profileActions";
import ProfilePage from "./ProfilePage";
import Modal from "react-modal";
import * as navigationTitleActions from "../../actions/navigationTitleActions";

class ProfileContainer extends React.Component {
  componentDidMount() {
    this.props.navActions.updateNavTitle("Profile");
    this.props.actions.getProfile();
  }
  render() {
    return (
      <div>
        {!this.props.profile.loading ? (
          <ProfilePage
            profile={this.props.profile}
            toast={this.props.profile.toast}
            directoryStatus={this.props.profile.directoryStatus}
            emailInfo={this.props.profile.emailInfo}
            faxInfo={this.props.profile.faxInfo}
            contactInfoLoading={this.props.profile.contactInfoLoading}
            toggleDirectoryHide={this.props.actions.toggleDirectoryHide}
            closeToast={this.props.actions.closeToast}
            updateContact={this.props.actions.updateContact}
            deleteContact={this.props.actions.deleteContact}
            addContact={this.props.actions.addContact}
            userData={JSON.parse(localStorage.getItem("data"))}
          />
        ) : (
          ""
        )}
        <Modal
          className="loading-modal"
          isOpen={this.props.profile.loading}
          ariaHideApp={false}
          contentLabel="Modal"
          shouldCloseOnOverlayClick={false}
        >
          <div
            className={
              this.props.profile.loading ? "centered-spinner" : "hidden"
            }
          >
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
        </Modal>
      </div>
    );
  }
}
function mapStateToProps(state) {
  return {
    profile: state.profile,
  };
}
function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    actions: bindActionCreators(profileActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ProfileContainer);
