import React from "react";
import Modal from "react-modal";
import Button from "muicss/lib/react/button";

export default class HideProfileConfirmModal extends React.Component {
  render() {
    return (
      <Modal
        className="confirmation-modal"
        ariaHideApp={false}
        contentLabel="Modal"
        isOpen={this.props.isOpen}
        onAfterOpen={this.props.onAfterOpen}
        onRequestClose={this.props.onRequestClose}
        shouldCloseOnOverlayClick={false}
      >
        <p className="bold-text confirmation-modal-header">
          Hide your full profile from directory?
        </p>
        {this.props.userData && this.props.userData.is_sales_broker ? (
          <p className="small-text accent-text-dark">
            <span className="bold-text">Important: </span>
            Sales brokers, please be aware that your choice to hide your profile
            will cause your contact information to be hidden when users search
            for properties that are associated with you.
          </p>
        ) : (
          ""
        )}
        <div className="confirmation-modal-btns">
          <Button
            variant="flat"
            onClick={this.props.onRequestClose.bind(this, false)}
          >
            Cancel
          </Button>
          <Button
            variant="flat"
            onClick={this.props.onRequestClose.bind(this, true)}
          >
            Hide
          </Button>
        </div>
      </Modal>
    );
  }
}
