import React from "react";
import PropTypes from "prop-types";
import Modal from "react-modal";
import Form from "muicss/lib/react/form";
import Row from "muicss/lib/react/row";
import Option from "muicss/lib/react/option";
import Select from "muicss/lib/react/select";
import Input from "muicss/lib/react/input";
import Button from "muicss/lib/react/button";

class AddContactModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectBoxValue: "",
      textBoxValue: "",
      toggleOn: false,
      errorMessage: "",
      formValid: false,
    };
    this.selectTypeOnChange = this.selectTypeOnChange.bind(this);
    this.textBoxOnChange = this.textBoxOnChange.bind(this);
    this.textBoxOnBlur = this.textBoxOnBlur.bind(this);
    this.toggleChangeHandler = this.toggleChangeHandler.bind(this);
    this.submitContact = this.submitContact.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.validateEmail = this.validateEmail.bind(this);
    this.validatePhone = this.validatePhone.bind(this);
  }

  validatePhone(value) {
    var re = /^(?:(?:\+?1\s*(?:[.-]\s*)?)?(?:\(\s*([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9])\s*\)|([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9]))\s*(?:[.-]\s*)?)?([2-9]1[02-9]|[2-9][02-9]1|[2-9][02-9]{2})\s*(?:[.-]\s*)?([0-9]{4})(?:\s*(?:#|x\.?|ext\.?|extension)\s*(\d+))?$/;
    return re.test(value);
  }

  validateEmail(value) {
    var re = /[^@]+@[^@]+\.[^@]+/;
    return re.test(value);
  }

  selectTypeOnChange(e) {
    this.setState({ selectBoxValue: e });
  }

  textBoxOnChange(e) {
    this.setState({
      textBoxValue: e.target.value,
    });
  }

  textBoxOnBlur(e) {
    if (this.props.type === "phone") {
      const result = this.validatePhone(e.target.value);
      if (!result) {
        this.setState({
          formValid: false,
          errorMessage: "Not a valid phone number",
        });
      }
      if (result) {
        this.setState({
          formValid: true,
          errorMessage: "",
        });
      }
    } else if (this.props.type === "email") {
      const result = this.validateEmail(e.target.value);
      if (!result) {
        this.setState({
          formValid: false,
          errorMessage: "Not a valid email",
        });
      }
      if (result) {
        this.setState({
          formValid: true,
          errorMessage: "",
        });
      }
    } else if (this.props.type === "fax") {
      this.setState({ textBoxValue: e.target.value });
      const result = this.validatePhone(e.target.value);
      if (!result) {
        this.setState({
          formValid: false,
          errorMessage: "Not a valid phone number",
        });
      }
      if (result) {
        this.setState({
          formValid: true,
          errorMessage: "",
        });
      }
    }
  }

  toggleChangeHandler(e) {
    this.setState({ toggleOn: e.target.checked });
  }

  closeModal(e) {
    e.preventDefault();
    this.resetModal();
  }

  submitContact(e) {
    e.preventDefault();
    const defaultValues = {
      fax: 4,
      email: 13,
      phone: 1,
    };
    let inputFormatted = null;
    if (this.props.type === "fax" || this.props.type === "phone") {
      inputFormatted = this.state.textBoxValue.replace(
        /(\d{3})(\d{3})(\d{4})/,
        "$1-$2-$3"
      );
    } else if (this.props.type === "email") {
      inputFormatted = this.state.textBoxValue.replace("\\s+", "");
    }
    this.props.addContact(
      this.state.selectBoxValue || defaultValues[this.props.type],
      inputFormatted,
      this.state.toggleOn
    );
    this.resetModal();
  }

  resetModal() {
    this.setState({
      selectBoxValue: "",
      textBoxValue: "",
      toggleOn: false,
      errorMessage: "",
      formValid: false,
    });
    this.props.onRequestClose();
  }

  render() {
    const customStyles = {
      content: {
        margin: "auto",
      },
    };

    const modalHeader = {
      fax: "Add New Fax",
      email: "Add New Email",
      phone: "Add New Number",
    };

    const options = {
      fax: [
        { label: "Office", code: 4 },
        { label: "Home", code: 9 },
      ],
      email: [
        { label: "Work", code: 13 },
        { label: "Personal", code: 15 },
      ],
      phone: [
        { label: "Office", code: 1 },
        { label: "Company", code: 7 },
        { label: "Home", code: 8 },
        { label: "Cell", code: 10 },
      ],
    };

    let dropdown = null;
    let input = null;
    if (this.props.type) {
      dropdown = options[this.props.type].map((option, i) => (
        <Option key={i} value={option.code} label={option.label} />
      ));
    }
    if (this.props.type === "fax") {
      input = (
        <Input
          label="Fax Number"
          hint="Enter your number"
          value={this.state.textBoxValue}
          onChange={this.textBoxOnChange}
          onBlur={this.textBoxOnBlur}
        />
      );
    } else if (this.props.type === "phone") {
      input = (
        <Input
          label="Phone Number"
          hint="Enter your number"
          value={this.state.textBoxValue}
          onChange={this.textBoxOnChange}
          onBlur={this.textBoxOnBlur}
        />
      );
    } else if (this.props.type === "email") {
      input = (
        <Input
          label="Email Address"
          hint="Enter your email"
          value={this.state.textBoxValue}
          onChange={this.textBoxOnChange}
          onBlur={this.textBoxOnBlur}
        />
      );
    }

    return (
      <Modal
        className="add-contact-modal"
        isOpen={this.props.isOpen}
        onRequestClose={this.props.onRequestClose}
        style={customStyles}
        ariaHideApp={false}
        contentLabel="Modal"
        shouldCloseOnOverlayClick={true}
      >
        <Form className="add-contact-modal-form">
          <Row className="add-contact-modal-header">
            <p>{modalHeader[this.props.type]}</p>
          </Row>
          <Row className="add-contact-form-info">
            <Select label="Type" onChange={this.selectTypeOnChange}>
              {dropdown}
            </Select>
            <div>
              {input}
              <span className="help-block">{this.state.errorMessage}</span>
            </div>
            <div className="toggle-group">
              <div className="toggle-switch">
                <input
                  type="checkbox"
                  id="unchecked1"
                  className="cbx hidden"
                  onChange={this.toggleChangeHandler}
                />
                <label htmlFor="unchecked1" className="lbl" />
              </div>
              <p>Hide from directory</p>
            </div>
            <div className="add-contact-modal-buttons">
              {this.state.formValid ? (
                <Button
                  variant="flat"
                  className="save-btn"
                  onClick={this.submitContact}
                >
                  Save
                </Button>
              ) : (
                <Button
                  variant="flat"
                  className="save-btn"
                  disabled={true}
                  onClick={this.submitContact}
                >
                  Save
                </Button>
              )}
              <Button
                variant="flat"
                className="cancel-btn"
                onClick={this.closeModal}
              >
                Cancel
              </Button>
            </div>
          </Row>
        </Form>
      </Modal>
    );
  }
}

AddContactModal.propTypes = {
  isOpen: PropTypes.bool,
  onRequestClose: PropTypes.func.isRequired,
};

export default AddContactModal;
