import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";

const ProfileContact = (props) => (
  <Row>
    <Col lg="3" md="3" sm="4" xs="4">
      <p className="profile-info-header">
        {props.category === "phone" ? (
          <i className="material-icons">phone</i>
        ) : props.category === "email" ? (
          <i className="material-icons">email</i>
        ) : props.category === "fax" ? (
          <i className="material-icons">print</i>
        ) : (
          ""
        )}
        {props.type}
      </p>
    </Col>
    <Col lg="8" md="8" sm="7" xs="7">
      <div>
        {props.contact}
        <i className={props.isPrimary ? "material-icons main" : "hidden"}>
          check_circle
        </i>
        {props.hide ? (
          <p
            className="show"
            onClick={() => props.showHideContact(props.cpId, props.category, 0)}
          >
            Show
          </p>
        ) : null}
      </div>
    </Col>
    <Col md="1" sm="1" xs="1">
      <div className="profile-page-dropdown">
        <i className="material-icons">more_vert</i>
        <div className="profile-dropdown-content">
          {props.typeCode !== "4" && props.typeCode !== "9" ? (
            <div
              href="#"
              className={props.isPrimary ? "disabled-main-link" : ""}
              onClick={() => props.setAsMain(props.cpId, props.category)}
            >
              <i className="material-icons main-dropdown">check_circle</i>
              Set as main
            </div>
          ) : (
            ""
          )}
          <div
            href="#"
            className={props.hide ? "disabled-main-link" : ""}
            onClick={() => props.showHideContact(props.cpId, props.category, 1)}
          >
            <img src="/images/icons/eye-off.png" alt="eye-off" />
            Hide from directory
          </div>
          <div
            href="#"
            onClick={() => {
              props.deleteContact(props.cpId);
            }}
          >
            <i className="material-icons profile-dropdown-delete">delete</i>
            Delete
          </div>
        </div>
      </div>
    </Col>
  </Row>
);

export default ProfileContact;
